t 0px 1px 3px rgba(0,0,0,0.1)\r\n\r\n    input[type=\"submit\"], button\r\n        width: 100%\r\n\r\n        //background\r\n        //box-shadow: inset 0 0 black\r\n        //transition: box-shadow 500ms\r\n\r\n        &:hover\r\n            \r\n            //box-shadow: inset 0 0 1em yellow \r\n\r\n    input[data-wrong=\"true\"]\r\n        border: 2px solid #ff00008c\r\n\r\n.accountFormWrap\r\n    display: flex\r\n    align-items: center\r\n    align-content: center\r\n    text-align: center\r\n    flex-flow: column nowrap\r\n    max-height: 100%\r\n    scrollbar-color: #535353 transparent\r\n\r\n    & p \r\n        margin-top: 0\r\n\r\n    ul \r\n        text-align: left\r\n\r\n    sub, sup\r\n        color: #888\r\n\r\n.usernameHighlight\r\n    background-color: #cacaca\r\n    padding: .25em .5em \r\n    color: #2b2828\r\n    border-radius: 1em\r\n\r\n.removeButton \r\n    background-color: #ff3434\r\n    color: white\r\n    border: none\r\n    border-radius: 2em\r\n    font-weight: bold\r\n    font-size: 1.5em\r\n    padding: .25em 1em\r\n    margin-top: 1.5em\r\n    font-family: inherit\r\n    transition: all 400ms\r\n    max-width:18.75em\r\n    max-height: 3.125em\r\n    overflow: hidden\r\n    white-space: break-word\r\n\r\n    svg\r\n        color: white\r\n\r\n    &:hover\r\n        box-shadow: inset 0 .125em .5em .25em #00000067\r\n\r\n    &.clicked, &:active\r\n        color: transparent\r\n        transform: translateY(-.25em)\r\n        max-width: 2em\r\n        max-height: 2em\r\n        padding: 12px 5px\r\n        box-shadow: inset 0 .125em .5em .25em #00000067\r\n\r\n.accountFormsAlign\r\n    display: flex\r\n    min-width: 60%\r\n    justify-content: space-between \r\n\r\n    & > div\r\n        flex-shrink: 1\r\n\r\n\r\n.registerForm\r\n    @extend .form\r\n    max-width: 300px\r\n    z-index: 0\r\n    position: relative\r\n\r\n.tabWrapper\r\n    width: max-content\r\n    margin: 0 auto\r\n\r\n.tabBar\r\n    display: flex\r\n    width: 300px\r\n\r\n.tab \r\n    flex: 1 1 0\r\n    padding: 1em\r\n    padding-bottom: .25em\r\n    padding-top: .25em\r\n    margin-bottom: .5em\r\n    font-size: 1.5em\r\n    color: #788080\r\n    transition: all 200ms\r\n    border-bottom: .25em transparent solid\r\n\r\n    &[aria-selected=\"true\"]\r\n        color: #cecece\r\n        border-bottom: .25em #cecece solid\r\n\r\n.brandButton \r\n    @extend .baseButton\r\n    padding: 1em\r\n    font-size: 2em\r\n    background-image: linear-gradient(135deg,#ff00fe, #00f3ff)\r\n\r\n.baseButton\r\n    &:disabled\r\n        cursor: not-allowed\r\n        color: #979797\r\n        background-color: transparent\r\n\r\n    box-sizing: border-box\r\n    max-width: 100%\r\n    width: 100%\r\n    border: 2px solid #535353\r\n    border-radius: 10px\r\n    \r\n    padding: 5px\r\n\r\n    background-color: #262626\r\n    color: white\r\n    \r\n    font-size: inherit  \r\n    outline: none\r\n\r\n    &:focus     \r\n        border-color: #acacac   \r\n\r\n.postButton \r\n    @extend .submitButton      \r\n    & button\r\n        z-index: 2\r\n\r\n        &::before\r\n            z-index: -1\r\n            background-image: linear-gradient(135deg, rgba(38,38,38,1), rgba(38,38,38,1), #d8db01, rgba(193,62,15,1))\r\n\r\n    &:not(.dull) button\r\n        &:disabled\r\n            color: #fffb00 \r\n            border-color: #fffb00\r\n\r\n.submitButton \r\n    /*display: flex*/\r\n    /*justify-content: space-between*/\r\n\r\n    & span\r\n        background-color: #ff00008c\r\n        padding: 1em\r\n        border-radius: 1em\r\n        color: white\r\n        display: block\r\n\r\n        sub, sup \r\n            color: #cecece\r\n\r\n\r\n    & button    \r\n        flex-grow: 0\r\n        flex-shrink: 0\r\n        height: 2.4375em\r\n\r\n        padding: .5em 1.5em \r\n        overflow: hidden\r\n        transition: all 200ms ease-out\r\n        font-weight: bold\r\n        width: max-content\r\n        float: right\r\n        position: relative\r\n        background-color: transparent\r\n        margin: 2px\r\n\r\n        &:active\r\n            \r\n        &::before\r\n            transition: 200ms ease-out\r\n            transition-property: top, left, border\r\n            content: \"\"\r\n            position: absolute\r\n            width: 315%\r\n            height: 315%\r\n            top: 0%\r\n            left: 0%\r\n            background: linear-gradient(135deg, rgba(38,38,38,1), rgba(38,38,38,1), #01db0c, #02420d)\r\n            background-position: 0% 0%\r\n            z-index: -1\r\n\r\n    &:not(.dull) button\r\n        &:hover, &:active\r\n            &::before\r\n                top: -200%\r\n                left: -200%\r\n            color: white\r\n            border-color: #00000000\r\n\r\n        &:disabled\r\n            height: 2.6875em\r\n            cursor: wait\r\n            color: lime\r\n            border: 4px double lime\r\n            margin: 0\r\n            animation: jumping 300ms ease 1\r\n            &::before\r\n                transition: none\r\n                animation: rotating 500ms infinite linear\r\n                top: -105%\r\n                left: -106%\r\n                height: 460%\r\n\r\n.destructiveButton \r\n    @extend .submitButton      \r\n    & button\r\n        border-color: #eb0808\r\n        z-index: 2\r\n        &::before\r\n            z-index: -1\r\n            background-image: linear-gradient(135deg, rgba(38,38,38,1), rgba(38,38,38,1),#830da3, #eb0808 )\r\n\r\n    &:not(.dull) button\r\n        &:disabled\r\n            color: #d60000\r\n            border-color: #d60000\r\n\r\n\r\n\r\n@keyframes rotating\r\n    from\r\n        transform: rotate(0deg)\r\n\r\n    to\r\n        transform: rotate(365deg)\r\n\r\n@keyframes jumping\r\n    0%\r\n        transform: translateY(0em)\r\n    50%\r\n        transform: translateY(-1em)\r\n    100%\r\n        transform: translateY(0em)"]}]);
